<template>
  <div id="tabletRegister">
    <h2></h2>
    <div>
      <table border='0' cellpadding='0' cellspacing='0' id="tab">
        <tr>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldNumber}}</td>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldName}}</td>
        </tr>
        <tr>
          <td colspan='6'></td>
          <td colspan='18' style="font-weight: normal">{{data.srldPosition}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.areaRedline}}</td>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldPlantType}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%">()</td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldArea}}</td>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{data.srldPopulation}}</td>
        </tr>
        <tr>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{getType(data.srldType)}}</td>
          <td colspan='6' style="width: 25%"></td>
          <td colspan='6' style="width: 25%;font-weight: normal">{{getTarget(data.target)}}</td>
        </tr>
        <tr>
          <td colspan='6'></td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldPlantType}}</td>
        </tr>
        <tr>
          <td colspan='6'></td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldActive}}</td>
        </tr>
        <tr>
          <td colspan='6'></td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldProblem}}</td>
        </tr>
        <tr>
          <td colspan='6'></td>
          <td colspan='18' style="font-weight: normal;text-align: left">{{data.srldControl}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { renderTime } from '@/utils/common'

export default {
  name: 'TabletRegister',
  data () {
    return {
      /*  */
      data: {},
      type: ''
    }
  },
  computed: {},
  props: {
    tabletData: {},
    /*  */
    codeData: {}
  },
  watch: {
    tabletData () {
      this.getTableData()
    }
  },
  mounted () {},
  methods: {
    /*  */
    renderTime (date) {
      return renderTime(date)
    },
    /*  */
    getType (data) {
      this.type = data
      if (data === '01') {
        return ''
      } else if (data === '02') {
        return ''
      } else {
        return ''
      }
    },
    /* target */
    getTarget (data) {
      if (this.type === '01') {
        if (data === '01') {
          return ''
        } else if (data === '02') {
          return ''
        } else if (data === '03') {
          return ''
        } else if (data === '04') {
          return ''
        } else if (data === '05') {
          return ''
        }
      } else if (this.type === '02') {
        if (data === '01') {
          return ''
        } else if (data === '02') {
          return ''
        } else if (data === '03') {
          return ''
        } else if (data === '04') {
          return ''
        }
      }
    },
    /*  */
    getTableData () {
      this.data = this.tabletData
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  #tabletRegister {
    height: 100%;
    overflow: hidden;
    &>div {
      height: calc(100% - 41px);
      overflow: auto;
    }
  }
  #tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 10px 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
