<template>
  <div class="mapTable" >
    <div class="btnBox" v-show="!spotTable" @click="spotTable=true">
      <i-button icon="ios-chatboxes"></i-button>
    </div>
    <div class="modelBox" v-if="spotTable">
      <p>
        <strong :class="{ active : tabState === 1 }" @click="switchTab(1)">
          
        </strong>
        <strong :class="{ active : tabState === 2 }" @click="switchTab(2)">
          
        </strong>
        <strong :class="{ active : tabState === 3 }" @click="switchTab(3)">
          
        </strong>
        <strong :class="{ active : tabState === 0 }"  @click="switchTab(0)">
          
        </strong>
        <strong :class="{ active : tabState === 4 }" @click="switchTab(4)">
          
        </strong>
        <Button type="primary" size="small" v-if="tabState === 0" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPointExcel" class="exportBtn"></Button>
        <Button type="primary" size="small" v-if="tabState === 1" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportRedLineExcel" class="exportBtn"></Button>
        <Button type="primary" size="small" v-if="tabState === 2" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportPreExcel" class="exportBtn"></Button>
        <Button type="primary" size="small" v-if="tabState === 3" style="float: right;margin:5px 0" icon="md-cloud-download" @click="exportBoardPreExcel" class="exportBtn"></Button>
      </p>
      <!---->
      <div class="table_box" v-if="tabState === 0">
        <Table :loading="loading1" border stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total1" :page-size="pageSize1"  size="small" class="thePage" @on-change="pageChange1" :current="pageNum1" show-total></Page>
        <!-- -->
        <pointInfoEdit ref="pointInfoEdit" :redLineInfoData="pointInfoData" @getTableData="getTableData"></pointInfoEdit>
      </div>
      <!---->
      <div class="table_box" v-if="tabState === 1">
        <Table :loading="loading2" border stripe :data="data2" :columns="columns2" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total2" :page-size="pageSize2"  size="small" class="thePage" @on-change="pageChange2" :current="pageNum2" show-total></Page>
        <!---->
        <div class="extract_box" :class="{regActive: registerTab}">
          <Button type="primary" @click="registerTab = false" size="small" style="position: absolute;right:8px;top:8px;"></Button>
          <redLineRegister :tabletData="tabletData" style="background: #fff;"></redLineRegister>
        </div>
        <!-- -->
        <redLineInfoEdit ref="redLineInfoEdit" :redLineInfoData="redLineInfoData" @getTableData="getRedlineData"></redLineInfoEdit>
      </div>
      <!---->
      <div class="table_box" v-if="tabState === 2">
        <Table  :loading="loading3" border stripe :columns="columns3" :data="data3" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total3" :page-size="pageSize3"  size="small" class="thePage" @on-change="pageChange3" :current="pageNum3" show-total></Page>
        <!-- -->
        <PreMarker ref="PreMarkerEdit" :PreMarkerData="PreMarkerData" :codeData="codeData" :redlineList="redlineList" @getTableData="getTableData3"></PreMarker>
      </div>
      <!---->
      <div class="table_box" v-if="tabState === 3">
        <Table :loading="loading4" border stripe :columns="columns4" :data="data4" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total4" :page-size="pageSize4"  size="small" class="thePage" @on-change="pageChange4" :current="pageNum4" show-total></Page>
        <!-- -->
        <PretLmBoard ref="PretLmBoardEdit" :PretLmBoardData="PretLmBoardData" :redlineList="redlineList" @getTableData="getTableData4"></PretLmBoard>
      </div>
      <!---->
      <div class="table_box" v-if="tabState === 4">
        <Form :model="statisticForm" :label-width="90" class="statisticForm">
          <FormItem prop="city" label="">
            <i-select v-model.trim="statisticForm.statisticType" style="width: 100%" placeholder="" @on-change="changeStatisticType">
              <i-option v-for="item in statisticList" :value="item.value">{{ item.label }}</i-option>
            </i-select>
          </FormItem>
        </Form>
        <div class="statisticBox">
          <!---->
          <BarChart1 ref="BarChart1" v-if="statisticForm.statisticType === '0'"></BarChart1>
          <!---->
          <BarChart2 ref="BarChart2" v-else-if="statisticForm.statisticType === '1'"></BarChart2>
          <!---->
          <BarChart3 ref="BarChart3" v-else-if="statisticForm.statisticType === '2'"></BarChart3>
          <!---->
          <BarChart4 ref="BarChart4" v-else-if="statisticForm.statisticType === '3'"></BarChart4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  /**
   * 
   */
  getPointList,
  exportPointExcel,
  /**
   * 
   */
  getPreMarkerList,
  exportPreExcel,
  /**
   * 
   */
  gePretLmBoardList,
  exportBoardPreExcel,
  /**
   * 
   */
  redlineData,
  // 
  getMenu,
  // 
  allRedlLine,
  // 
  exportRedLineExcel
} from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import { cacuLonLat } from '@/utils/common'
import BarChart1 from './BarChart1.vue'
import BarChart2 from './BarChart2.vue'
import BarChart3 from './BarChart3.vue'
import BarChart4 from './BarChart4.vue'
// 
import redLineRegister from './redLine_register'
// 
import redLineInfoEdit from './redLine_edit'
// 
import PretLmBoard from './PretLmBoard_edit'
// 
import PreMarker from './PreMarker_edit'
// 
import pointInfoEdit from './point_edit'
export default {
  name: 'MapTable',
  data () {
    return {
      /*  */
      tableHeight: Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32,
      /*  */
      spotTable: true,
      /*  */
      telescopic: true,
      /**
       * 
       */
      /*  */
      columns1: [
        {
          title: '',
          key: 'lpCode',
          width: 180,
          fixed: 'left',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.lpLon, params.row.lpLat, params.row.lpId, 'inflectionPoint')
                  }
                }
              }, params.row.lpCode)
            ])
          }
        },
        {
          title: '',
          key: 'lpLon',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLon))
          }
        },
        {
          title: '',
          key: 'lpLat',
          minWidth: 120,
          align: 'center',
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.lpLat))
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.pointInfoData = params.row
                    this.$refs.pointInfoEdit.pointRebuild = true
                    this.$refs.pointInfoEdit.dataHandle(params.row)
                    console.log(this.pointInfoData)
                  }
                }
              }, '')
            ])
          }
        }
      ],
      /*  */
      data1: [],
      /*  */
      pageNum1: 1,
      /*  */
      pageSize1: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /*  */
      total1: 0,
      /*  */
      loading1: false,
      /**
       * 
       */
      /*  */
      loading2: false,
      /*  */
      pageNum2: 1,
      /*  */
      pageSize2: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /*  */
      total2: 0,
      /*  */
      data2: [],
      /*  */
      columns2: [
        {
          title: '',
          key: 'srldNumber',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd2(params.row.srldId)
                  }
                }
              }, params.row.srldNumber)
            ])
          }
        },
        {
          title: '',
          key: 'areaRedline',
          align: 'center',
          width: 150
        },
        {
          title: '',
          key: 'srldPlantType',
          align: 'center',
          width: 150
        },
        {
          title: '',
          key: 'srldTarget',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            let str = ''
            if (params.row.srldType === '01') {
              str += ''
              if (params.row.target === '01') {
                str += ''
              } else if (params.row.target === '02') {
                str += ''
              } else if (params.row.target === '03') {
                str += ''
              } else if (params.row.target === '04') {
                str += ''
              } else if (params.row.target === '05') {
                str += ''
              }
            } else if (params.row.srldType === '02') {
              str += ''
              if (params.row.target === '01') {
                str += ''
              } else if (params.row.target === '02') {
                str += ''
              } else if (params.row.target === '03') {
                str += ''
              } else if (params.row.target === '04') {
                str += ''
              }
            }
            return h('div', str)
          }
        },
        {
          title: '()',
          key: 'srldArea',
          align: 'center',
          minWidth: 120
        },
        {
          title: '',
          key: 'isBs',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.tabletData = params.row
                    this.registerTab = true
                  }
                }
              }, '')
            ])
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              // 
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.redLineInfoData = params.row
                    this.$refs.redLineInfoEdit.redLineRebuild = true
                  }
                }
              }, '')
            ])
          }
        }
      ],
      /**
       * 
       */
      /*  */
      columns3: [
        {
          title: '',
          key: 'jzNumber',
          align: 'center',
          width: 180,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd3(params.row.longitude, params.row.latitude, params.row.objectId, 'inflectionJiebei')
                  }
                }
              }, params.row.jzNumber)
            ])
          }
        },
        {
          title: '',
          key: 'jzKh',
          align: 'center',
          minWidth: 180
        },
        {
          title: '',
          key: 'redlineName',
          align: 'center',
          minWidth: 170
        },
        {
          title: '',
          key: 'placeName',
          align: 'center',
          minWidth: 150
        },
        {
          title: '',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.PreMarkerData = params.row
                    this.$refs.PreMarkerEdit.PreMarkerRebuild = true
                  }
                }
              }, '')
            ])
          }
        }
      ],
      /*  */
      data3: [],
      /*  */
      pageNum3: 1,
      /*  */
      pageSize3: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /*  */
      total3: 0,
      /*  */
      loading3: false,
      /*  */
      /**
       * 
       */
      /*  */
      columns4: [
        {
          title: '',
          key: 'number',
          align: 'center',
          width: 150,
          fixed: 'left',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd4(params.row.longitude, params.row.latitude, params.row.objectId, 'inflectionBiaopai')
                  }
                }
              }, params.row.number)
            ])
          }
        },
        {
          title: '',
          key: 'redlineName',
          align: 'center',
          minWidth: 200
        },
        {
          title: '',
          key: 'longitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.longitude))
          }
        },
        {
          title: '',
          key: 'latitude',
          align: 'center',
          minWidth: 120,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.latitude))
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('div', [
              // 
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.PretLmBoardData = ''
                    this.PretLmBoardData = params.row
                    this.$refs.PretLmBoardEdit.PretLmBoardRebuild = true
                  }
                }
              }, '')
            ])
          }
        }
      ],
      /*  */
      data4: [],
      /*  */
      pageNum4: 1,
      /*  */
      pageSize4: Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40),
      /*  */
      total4: 0,
      /*  */
      loading4: false,
      /* tab */
      tabState: 1,
      // 
      statisticForm: {
        statisticType: '1'
      },
      // 
      statisticList: [
        {
          label: '',
          value: '0'
        },
        {
          label: '',
          value: '1'
        },
        {
          label: '',
          value: '2'
        },
        {
          label: '',
          value: '3'
        }
      ],
      /*  */
      registerTab: false,
      /*  */
      tabletData: {},
      /*  */
      redLineInfoData: {},
      /*  */
      PretLmBoardData: {},
      /*  */
      PreMarkerData: {},
      /*  */
      pointInfoData: {},
      /*  */
      redlineList: [],
      /*  */
      codeData: []
    }
  },
  props: {
    /*  */
    obj: {
      default: {
        code: '',
        param: ''
      }
    },
    param: {},
    srId: null,
    dataChange: {}
  },
  components: {
    BarChart1,
    BarChart2,
    BarChart3,
    BarChart4,
    // 
    redLineRegister,
    // 
    redLineInfoEdit,
    // 
    PretLmBoard,
    // 
    PreMarker,
    // 
    pointInfoEdit
  },
  watch: {
    // 
    obj () {
      if (this.tabState === 0) {
        this.data1 = []
        this.getTableData()
      } else if (this.tabState === 1) {
        this.data2 = []
        this.getRedlineData()
      } else if (this.tabState === 2) {
        this.data3 = []
        this.getTableData3()
      } else if (this.tabState === 3) {
        this.data4 = []
        this.getTableData4()
      }
    },
    /**
     * 
     */
    srId (val) {
      if (this.tabState === 0) {
        this.pageNum1 = 1
        this.getTableData()
      } else if (this.tabState === 1) {
        this.pageNum2 = 1
        this.getRedlineData()
      } else if (this.tabState === 2) {
        this.pageNum3 = 1
        this.getTableData3()
      } else if (this.tabState === 3) {
        this.pageNum4 = 1
        this.getTableData4()
      }
    },
    pageNum1 () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    },
    /**
     * 
     */
    pageNum3 () {
      this.getTableData3()
    },
    /**
     * 
     */
    pageNum4 () {
      this.getTableData4()
    },
    /**
     * 
     */
    pageNum2 () {
      this.getRedlineData()
    }
  },
  created () {
    this.switchTab(this.tabState)
  },
  mounted () {
    /*  */
    window.addEventListener('resize', () => {
      // 
      this.tableHeight = Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32
      // tab
      this.pageSize1 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize2 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize3 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.pageSize4 = Math.floor((Math.floor((window.innerHeight - 307 - 32) / 40) * 40 + 32) / 40)
      this.switchTab(this.tabState)
    }, false)
    // 
    this.getCodeMenu()
  },
  methods: {
    /*  */
    getTableData () {
      this.loading1 = true
      getPointList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum1,
          pageSize: this.pageSize1,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total1 = res.data.data.total
        // 
        this.$emit('getPointData', res.data.data.rows, 'inflectionPoint')
        this.loading1 = false
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    ondbclickTd (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /*  */
    pageChange1 (e) {
      this.pageNum1 = e
    },
    /*  */
    exportPointExcel () {
      exportPointExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /**
     * 
     */
    /*  */
    getTableData3 () {
      this.loading3 = true
      getPreMarkerList({
        accessToken: getToken(),
        validate: true,
        data: {
          pageNum: this.pageNum3,
          pageSize: this.pageSize3,
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        this.data3 = res.data.data.rows
        this.total3 = res.data.data.total
        // 
        this.$emit('getPointData', res.data.data.rows, 'inflectionJiebei')
        this.loading3 = false
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    ondbclickTd3 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /*  */
    pageChange3 (e) {
      this.pageNum3 = e
    },
    /*  */
    exportPreExcel () {
      exportPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /**
     * 
     */
    /*  */
    getTableData4 () {
      this.loading4 = true
      gePretLmBoardList({
        data: {
          pageNumber: this.pageNum4,
          pageSize: this.pageSize4,
          boardNum: this.obj.param,
          code: this.obj.code
        }
      }).then((res) => {
        this.data4 = res.data.data.row
        this.total4 = res.data.data.total
        // 
        this.$emit('getPointData', res.data.data.row, 'inflectionBiaopai')
        this.loading4 = false
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    ondbclickTd4 (lon, lat, id, layerId) {
      this.$emit('ondbclickDingwei', lon, lat, id, layerId)
    },
    /*  */
    pageChange4 (e) {
      this.pageNum4 = e
    },
    /*  */
    exportBoardPreExcel () {
      exportBoardPreExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /*  */
    getRedlineData () {
      this.loading2 = true
      redlineData({
        data: {
          pageNum: this.pageNum2,
          pageSize: this.pageSize2,
          code: this.obj.code,
          redLineNum: this.obj.param
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.total2 = res.data.data.total
          this.data2 = res.data.data.rows
          // 
          this.$emit('getPointData', res.data.data.rows, '')
          this.loading2 = false
        }
      })
    },
    /*  */
    ondbclickTd2 (id) {
      this.$emit('clickRedLineTable', id)
    },
    /*  */
    pageChange2 (e) {
      this.pageNum2 = e
    },
    /*  */
    switchTab (state) {
      this.$emit('update:dataChange', state)
      this.tabState = state
      if (state === 0) {
        this.getTableData()
      } else if (state === 1) {
        this.getRedlineData()
      } else if (state === 2) {
        this.getTableData3()
      } else if (state === 3) {
        this.getTableData4()
        /* axios:  */
        this.getRedLineList()
      }
    },
    /*  */
    changeStatisticType (val) {
     console.log(val)
    },
    /* axios:  */
    getRedLineList () {
      allRedlLine({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.redlineList = res.data.data
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /* axios:  */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.codeData = res.data.data
          this.codeData = this.valComCode(this.codeData)
        }
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    },
    /* value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    },
    /*  */
    exportRedLineExcel () {
      exportRedLineExcel({
        data: {
          code: this.obj.code,
          param: this.obj.param
        }
      }).then((res) => {
        let data = res.data
        if (!data) {
          return
        }
        let link = document.createElement('a')
        link.style.display = 'none'
        let url = res.data.data.filepath
        link.href = '/epr' + url
        link.setAttribute('download', url.split('/')[url.split('/').length - 1])
        document.body.appendChild(link)
        link.click()
      }).catch(() => {
        this.$Notice.error({ title: '' })
      })
    }
  }
}
</script>

